body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.screen {
  width: 100vw;
  height: 100vh;
  background-color: #274a60;
  //background-image: linear-gradient(180deg, #8BC6EC 0%, #9599E2 50%);
  overflow: hidden;
  display: flex;
  align-items: center;
  padding: 5vh;
  flex-direction: row;
  box-sizing: border-box;
}

.title-container {
  width: 100%;
  box-sizing: border-box;
}

.title {
  font-size: 5vh;
  font-weight: 100;
  text-transform: uppercase;
  color: white;
}

.subtitle {
  font-size: 4vh;
  font-weight: 200;
  color: white;
}



